
.content {
    width: 30vw;
    height: 100vh;
    margin: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
}
.nav {
    -ms-flex-pack: end;
        justify-content: flex-end;
    display: -ms-flexbox;
    display: flex;
}

@media (max-width: 480px){
    .content{
        width: 90vw;
        margin: auto;
    }
}
@media (max-width: 768px){
    .content{
        width: 80vw;
        margin: auto;
    }
}
@media (max-width: 1024px){
    .content{
        width: 60vw;
        margin: auto;
    }
}




